import React from 'react';

import Viewer from 'viewer/viewerComponent';

export default class ModelViewer extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const urlParams = new URLSearchParams(window.location.search);

        const projectFile = urlParams.get('projectFile');
        const glbFile = urlParams.get('glbFile');

        return (         
            <Viewer style={{marginTop: '60px'}} width="100%" height="100%" projectFile={projectFile} glbFile={glbFile} />
        );
    }
}
